import Teaser from "components/Template/Teaser";
import Main from "layout/Main";
import React from "react";

export default function OfferOne(props) {
  return (
    <Main
      title="Home"
      description=""
      // @ts-ignore
      lang={props.lang}
    >
      <Teaser
        fontColor="white"
        lang={props.lang}
        heading_fr="Traduction, correction, rdaction"
        heading_de="" // TODO
        contents_fr=""
        contents_de=""
        button_fr=""
        button_de=""
        button_ref=""
      ></Teaser>

      <div id="offer-one-main" className="d-flex bg-light p-5 m-md-3">
        <div className="col-md-2"></div>
        <div className="col-md-8">
          <div id="subtitle" className="d-flex">
            <div className="col-md-2"></div>
            <div className="col-md-8">
              <h6 className="text-center pb-4">
                Nous rdigeons, corrigeons, traduisons et affinons vos textes
                (allemand, franais, anglais, luxembourgeois) avec comptence et
                efficacit.
              </h6>
            </div>
          </div>
          <div className="border-top border-2 text-center p-5 text-center">
            <p>
              Texture vous propose son expertise dans le domaine rdactionnel.
            </p>
          </div>
          <div>
            <p>Nos points forts :</p>
            <p>
              <ul>
                <li className="m-3">
                  Expertise: Nous avons accumul des annes dexprience en ce
                  qui concerne la correction, la rdaction et de la traduction
                  pour diverses publications avec nos clients au Luxembourg et
                  au-del.
                </li>
                <li className="m-3">
                  Multilinguisme: Nous offrons une communication et des
                  services en luxembourgeois, allemand, franais et anglais. En
                  outre, nous travaillons en rseau avec des professionnels de
                  diffrents domaines et de diffrentes langues pour satisfaire
                  au mieux vos besoins.
                </li>
                <li className="m-3">
                  Prcision: Tout travail de texte demande une certaine
                  rigueur. Notre but est de vous fournir des textes facilement
                  lisibles, comprhensibles et dune qualit linguistique
                  impeccable.
                </li>
              </ul>
            </p>
          </div>
          <div className="border-top border-2 mt-5 pt-5 text-center">
            <p>
              <em>
                <strong>
                  Texture vous propose un entretien gratuit pour valuer vos
                  besoins. Demandez-nous un devis.
                </strong>
              </em>
            </p>
            <p>
              <a href="tel:+352 691 320 556">+352 691320556</a>
            </p>
            <p>
              <a href="mailto:info@texture.lu">info@texture.lu</a>
            </p>
          </div>

          <div>
          <div className="container border-top">
            <section className="customer-logos slider">
              <div className="slide"><img src="static/logo1.png" alt=""/></div>
              <div className="slide"><img src="static/logo2.png" alt=""/></div>
              <div className="slide"><img src="static/logo3.png" alt=""/></div>
              <div className="slide"><img src="static/logo4.png" alt=""/></div>
              <div className="slide"><img src="static/logo5.png" alt=""/></div>
              <div className="slide"><img src="static/logo6.png" alt=""/></div>
              <div className="slide"><img src="static/logo7.png" alt=""/></div>
              <div className="slide"><img src="static/logo8.png" alt=""/></div>
            </section>
          </div>
          </div>
        </div>
      </div>
    </Main>
  );
}
